@font-face {
    font-family: "special-elite";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
:root {
    /* Backgrounds */
    --color-bg-primary: #2A3B4C; /* Deep Ocean Blue-Grey */
    --color-bg-secondary: #4A5568; /* Charcoal Grey (for cards, UI elements) */

    /* Text & Content */
    --color-text-primary: #D0D6E2; /* Off-White/Light Grey */

    /* Accents & Interactivity */
    --color-accent-primary: #6CC3D5; /* Bright Aqua Blue (for interactive elements, radar) */
    --color-accent-secondary: #FF6F61; /* Coral Red (for warnings, actions) */

    /* Subtle Details */
    --color-detail-subtle: #5E7A90; /* Muted Blue-Grey (for lines, borders) */

    --cell-size: 45px;
}

body {
    font-family: "special-elite";
    background-color: var(--color-bg-primary);
}

header {
    display: flex;
}

.game-title {
    margin:0 auto;
}

h1, h2, h3, p {
    color:var(--color-text-primary);
}

h1, h2, h3 {
    font-family: "american-captain";
    
}

h1 {
    font-size:3.5rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 2rem;
}

p {
    font-size:1rem;
}

main {
    width:75%;
    margin:0 auto;
    padding-top:3rem;
}
.game-board-container {
    display:flex;
    justify-content: space-around;
    
}

.game-board {
    display:grid;
    grid-template-rows: repeat(10, var(--cell-size));
    grid-template-columns: repeat(10, var(--cell-size));
    gap:2px;
}

.grid-cell {
    border: 1px solid var(--color-detail-subtle);
    border-radius: 4.5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ship-occupied {
    background-color: var(--color-bg-secondary)
}

.grid-cell p {
    font-size:2rem;
    padding-top: .5rem;
}

.disabled-game-board {
    opacity: .5;
}

.disabled-game-board .grid-cell {
    cursor:not-allowed
}

.player-emojis {
    display:grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    justify-content: space-around;
    margin-bottom: 2rem;
}

.player-emoji {
    font-size:3rem;
}

.x-mark {
    font-weight: 600;
}

.light-x-mark {
    opacity: .6;
}

.hit-x-mark {
    color:var(--color-accent-secondary);
}

.ship-destroyed {
    background-color: var(--color-accent-secondary);
}

.end-game-modal {
    background-color: var(--color-bg-secondary);
    width:600px;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
    border-radius: 1.5rem;
    border: 1px solid var(--color-detail-subtle);
    display:grid;
    grid-template-rows: 1fr 1fr 60px;
    padding:2rem;
    row-gap: 2rem;
}

.loser {
    color:var(--color-accent-secondary)
}

.winner {
    color:var(--color-accent-primary);
}

.end-game-heading {
    justify-self: center;
}

.end-game-sub {
    font-size:1.4rem;
    place-self: center;
    text-align: center;
}

.btn {
    padding:.5rem 1rem;
    border-radius: .5rem;
    cursor: pointer;
    font-weight: 700;
    font-size: 1.25rem;
}

.close-modal-btn {
    justify-self: center;
    border: 1px solid var(--color-detail-subtle);
    background-color: var(--color-bg-primary); 
    color:var(--color-text-primary);
}

.welcome-heading, .welcome-heading + .typed-cursor{
    font-size:3rem;
    font-weight: 500;
    color:var(--color-text-primary);
}

.welcome-heading-container {
    justify-self: center;
}
.name-collection-screen {
    padding-top: 5rem;
    width:1100px;
    margin: 0 auto;
}

.name-collection-form {
    display: grid;
    grid-template-rows: 1fr 3.5rem 1fr;
    row-gap: 3rem;
}

.name-input {
    padding: 1.25rem .75rem;
    justify-self: center;
    border-radius: .5rem;
    background-color: var(--color-bg-secondary);
    border: 1px solid var(--color-detail-subtle);
    outline: none;
    transition: box-shadow .25s;
    color:var(--color-text-primary);
    font-size: 1.5rem;
}

.name-input:focus {
    box-shadow: 0px 0px 10px var(--color-bg-secondary);
}

.start-game-button {
    width:150px;
    border: 1px solid var(--color-detail-subtle);
    margin:0 auto;
    background-color: var(--color-accent-primary);
    color:var(--color-bg-primary);
}

.ship-placement-screen {
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    column-gap: 2rem;
}

#sp-left-col {
    justify-self: stretch;
}

.placement-instructions {
    color:var(--color-text-primary);
    font-size:1.2rem;
    margin-bottom: 1rem;
}

.place-randomly-btn {
    background-color: var(--color-accent-primary);
    border: 1px solid var(--color-detail-subtle);
    color:var(--color-bg-primary);
    margin:0 auto 2rem auto;
    display: block;
}

.placeable-grid-cell {
    width:45px;
    aspect-ratio: 1/1;
    display: inline-block;
}

.can-drop {
    background-color: var(--color-accent-primary);
}

.cannot-drop {
    background-color: var(--color-accent-secondary);
}

.rotate-icon {
    width:2.5rem;
    cursor:pointer;
    margin-left: auto;
}

.ship-container {
    display:grid;
}

.horizontal-ship-container {
    grid-template-rows: repeat(5, 1fr);
}

.vertical-ship-container {
    grid-template-columns: repeat(5, 1fr);
}

.inline-grid-cell {
    display: inline-block;
}

.block-grid-cell {
    display: block;
}

.hide {
    display:none;
}

.toast-container {
    position: absolute;
    bottom: 2rem;
    right: 2rem;
}

.toast {
    background-color: var(--color-accent-secondary);
    padding: .75rem 1.25rem;
    color: var(--color-bg-primary);
    font-weight: 600;
    opacity: 0;
    transition: opacity .75s, height .75s ;
    height: 0;
}

.toast-show {
    opacity: 1;
    height:auto;
}

.game-screen {
    margin-bottom: 3rem;
}

.bot-emoji {
    position: relative;
}

.thinking::after {
    content: "";
    width:75px;
    height:75px;
    position: absolute;
    display:block;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: contain;
    background-repeat:no-repeat;
    left:50px;
    top:-50px;
